export default {
  data() {
    return {
      email: "",
      disabled: true,
      showPositionValue: false, //提示信息
      toastContent: ""
    };
  },
  methods: {
    //按钮是否禁用
    keyDown() {
      if (this.$refs.refcode.valid == true && this.email != "") {
        this.disabled = false;
      } else {
        this.disabled = true;
      }
    },
    //提示框
    submitFun() {
      var that = this,
        formData,
        formData1;
      if (
        that.email == "" ||
        !/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(
          that.email
        )
      ) {
        that.showPositionValue = true;
        that.toastContent = "请您输入正确的邮箱";
      } else {
        formData1 = {
          email:  that.email,
          type: "2",
          sign: that.$store.state.user.sign,
          timestamp: that.$store.state.user.timestamp,
          nonce: that.$store.state.user.nonce
        };
        // //校验邮箱和手机
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/user/check-user",
            data: that.$qs.stringify(formData1)
          })
          .then(function(response) {
            if (response.code == 204) {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
            } else {
              formData = {
                email: that.email,
                type: "2",
                access_token: that.$store.state.user.token
              };
              that
                .$request({
                  method: "POST",
                  url: that.$baseURL + "/personal/get-code",
                  data: that.$qs.stringify(formData)
                })
                .then(function(response) {
                  if (response.code == 0) {
                    that.$router.push({
                      name: "setUpValidCode",
                      params: {
                        email: that.email,
                        type: "2"
                      }
                    });
                  } else {
                    that.showPositionValue = true;
                    that.toastContent = "验证码" + response.errmsg;
                  }
                })
                .catch(function(error) {});
            }
          })
          .catch(function(error) {});
      }
    }
  }
};



